.App {
  /* text-align: center; */
  font-family: "Helvetica Neue";
  background-color: #FAA916;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

/* HEADER */

.header {
  font-size: 50px;
  
}

/* QUESTION CONTAINER */

.question-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.question-container .buzzer {
  background: -webkit-gradient(linear, left top, left bottom, from(#da4f49), to(#b73a34));
  background: -webkit-linear-gradient(#da4f49, #b73a34);
  background: -o-linear-gradient(#da4f49, #b73a34);
  background: linear-gradient(#da4f49, #b73a34);
  color: white;
  font-size: 16px;
  padding: 4px 12px;
  border-radius: 5px;
}

.question-container .guess-field {
  background-color: #E7DFE8;
  padding: 4px;
}
.question-container .individual-question {
  padding: 30px;
  border: 5px solid;
  border-radius: 25px;
  width: 700px;
}


